.filters-section {
  margin-bottom: 2rem;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.filters-section .filters-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  position: relative;
}
.filters-section .filters-container .search-input-wrapper .search-input {
  position: relative;
  width: 100%;
}
.filters-section .filters-container .search-input-wrapper .search-input .search-field {
  width: 100%;
  padding: 1rem 1rem 1rem 3rem;
  border: 2px solid #e5e7eb;
  border-radius: 12px;
  font-size: 1rem;
  background: #f9fafb;
  transition: all 0.3s ease;
  font-weight: 500;
}
.filters-section .filters-container .search-input-wrapper .search-input .search-field:focus {
  outline: none;
  border-color: #2563eb;
  background: white;
  box-shadow: 0 0 0 4px rgba(37, 99, 235, 0.1);
  transform: translateY(-1px);
}
.filters-section .filters-container .search-input-wrapper .search-input .search-field::placeholder {
  color: #9ca3af;
  font-weight: 400;
}
.filters-section .filters-container .search-input-wrapper .search-input .search-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: #6b7280;
  font-size: 1.25rem;
}
.filters-section .filters-container .filter-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
}
.filters-section .filters-container .filter-grid .filter-dropdown .filter-label {
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  margin-bottom: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.filters-section .filters-container .filter-grid .filter-dropdown .filter-label svg {
  color: #6b7280;
  margin-right: 0.5rem;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative {
  position: relative;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button {
  width: 100%;
  padding: 0 1rem;
  text-align: left;
  border: 2px solid #e5e7eb;
  border-radius: 12px;
  background: white;
  color: #374151;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 56px;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button:hover {
  border-color: #d1d5db;
  background: #f9fafb;
  transform: translateY(-1px);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 4px rgba(37, 99, 235, 0.1);
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button.active {
  border-color: #2563eb;
  background: #eff6ff;
  box-shadow: 0 0 0 4px rgba(37, 99, 235, 0.1);
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .placeholder-text {
  color: #9ca3af;
  font-style: italic;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .selected-option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .selected-option .status-indicator,
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .selected-option .priority-indicator,
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .selected-option .category-indicator,
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .selected-option .asset-indicator,
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .selected-option .location-indicator {
  width: 2rem;
  height: 2rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .selected-option .selected-text {
  color: #111827;
  font-weight: 500;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .chevron-icon {
  width: 1.25rem;
  height: 1.25rem;
  color: #9ca3af;
  transition: transform 0.3s ease;
  flex-shrink: 0;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button .button-content .chevron-icon.active {
  transform: rotate(180deg);
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu {
  position: absolute;
  top: calc(100% + 0.5rem);
  left: 0;
  right: 0;
  background: white;
  border: 2px solid #e5e7eb;
  border-radius: 12px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  z-index: 50;
  max-height: 20rem;
  overflow: auto;
  animation: slideDown 0.2s ease-out;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-header {
  padding: 1rem 1rem 0.75rem;
  font-size: 0.75rem;
  font-weight: 700;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background: #f9fafb;
  border-radius: 12px 12px 0 0;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e5e7eb;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-header svg {
  margin-right: 0.5rem;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option {
  width: 100%;
  padding: 0.75rem 1rem;
  border: none;
  background: transparent;
  color: #374151;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f3f4f6;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option:last-child {
  border-bottom: none;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option:hover {
  background-color: #f8fafc;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option:focus {
  background-color: #f8fafc;
  outline: none;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option.selected {
  background-color: #eff6ff;
  color: #1d4ed8;
  font-weight: 500;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option .option-content {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  text-align: start;
  width: 100%;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option .option-content .option-icon {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option .option-content .option-text {
  flex: 1;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option .option-content .option-text .option-label {
  font-weight: 500;
  color: #111827;
  margin-bottom: 0.25rem;
}
.filters-section .filters-container .filter-grid .filter-dropdown .relative .dropdown-menu .dropdown-option .option-content .option-text .option-description {
  font-size: 0.75rem;
  color: #6b7280;
  line-height: 1.2;
}
.filters-section .filters-container .action-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #e5e7eb;
}
.filters-section .filters-container .action-buttons .action-btn {
  padding: 0.75rem 1.5rem;
  border: 2px solid transparent;
  border-radius: 8px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.filters-section .filters-container .action-buttons .action-btn:hover {
  transform: translateY(-1px);
}
.filters-section .filters-container .action-buttons .action-btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}
.filters-section .filters-container .action-buttons .action-btn.secondary {
  background: white;
  border-color: #d1d5db;
  color: #6b7280;
}
.filters-section .filters-container .action-buttons .action-btn.secondary:hover {
  border-color: #9ca3af;
  color: #374151;
  background: #f9fafb;
}
.filters-section .filters-container .action-buttons .action-btn.primary {
  background: #2563eb;
  color: white;
}
.filters-section .filters-container .action-buttons .action-btn.primary:hover {
  background: #1d4ed8;
  box-shadow: 0 4px 6px -1px rgba(37, 99, 235, 0.3);
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 1400px) {
  .filters-section .filters-container .filter-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
  }
}
@media (max-width: 1200px) {
  .filters-section .filters-container .filter-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
}
@media (max-width: 768px) {
  .filters-section {
    padding: 1.5rem;
    margin-bottom: 1.5rem;
  }
  .filters-section .filters-container {
    gap: 1.5rem;
  }
  .filters-section .filters-container .filter-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .filters-section .filters-container .action-buttons {
    flex-direction: column;
    gap: 0.75rem;
  }
  .filters-section .filters-container .action-buttons .action-btn {
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .filters-section {
    padding: 1rem;
    border-radius: 12px;
  }
  .filters-section .filters-container {
    gap: 1rem;
  }
  .filters-section .filters-container .search-input-wrapper .search-input .search-field {
    padding: 0.875rem 0.875rem 0.875rem 2.75rem;
    font-size: 0.875rem;
  }
  .filters-section .filters-container .search-input-wrapper .search-input .search-icon {
    font-size: 1rem;
    left: 0.875rem;
  }
  .filters-section .filters-container .filter-grid {
    gap: 0.75rem;
  }
  .filters-section .filters-container .filter-grid .filter-dropdown .filter-label {
    font-size: 0.75rem;
    margin-bottom: 0.5rem;
  }
  .filters-section .filters-container .filter-grid .filter-dropdown .relative .filter-button {
    padding: 0.875rem;
    min-height: 48px;
    font-size: 0.8rem;
  }
}
